<template>
  <div>
    <div class="bg-green-200 my-4 p-16">

      <input id="q" class="m-5 bg-pink-200 p-3 shadow-md border-2 border-pink-300 text-black italic" type="text" placeholder="Enter your query...">

      <button @click="getContent" class="bg-teal-300 hover:bg-teal-400 m-5 p-3 shadow-md border-2 border-teal-400 rounded-lg cursor-pointer">Search</button>

    </div>
    <div>
      <SearchResults :books="books" />
    </div>
  </div>
</template>

<script>
const axios = require('axios');
import SearchResults from './SearchResults.vue'

export default {
  name: 'Search',
  components:{SearchResults},
  data(){
    return{
      books:null
    }
  },
  methods:{
    async getContent(){
      var qry = document.getElementById("q")
      var q = qry.value.replace(new RegExp(' ', 'g'), '+')
      var link ="https://www.googleapis.com/books/v1/volumes?maxResults=40&q="+q
      var r = await axios.get(link)
      if(r.status == 200){
        this.books=r.data.items
      }

    }
  },

}
</script>

